import React from 'react';
import { Link } from 'gatsby';
import { string2html } from '../utils/string2html';
import camelCase from '../utils/camelCase';
import { formatReadingTime } from '../utils/readingTime';
import Right from './svg/arrowRight';
import Left from './svg/arrowLeft';

const BlogPostTemplate = ({ meta, children }) => {
  const content =
    typeof children === 'string' ? string2html(children) : children;

  return (
    <main className="post">
      <article>
        <section className="post__header">
          <h1>{meta.title}</h1>
          <span>
            <time datetime={meta.date}>{meta.date}</time>
            {` â€¢ ${formatReadingTime(meta.words)}`}
          </span>
          {meta.tags && meta.tags.length > 0 && (
            <div className="post__tags">
              {meta.tags.map((t, i) => (
                <Link key={i} to={`/tags/${camelCase(t)}`}>
                  {t.toLowerCase()}
                </Link>
              ))}
            </div>
          )}
        </section>

        <section className="card post__body">{content}</section>

        <section className="post__footer">
          {meta.next && !meta.prev && <div />}
          {meta.prev && (
            <Link to={meta.prev.link} className="previous">
              <Left />
              {meta.prev.title}
            </Link>
          )}
          {meta.next && (
            <Link to={meta.next.link} className="next">
              {meta.next.title}
              <Right />
            </Link>
          )}
        </section>
      </article>
    </main>
  );
};

export default BlogPostTemplate;
